import React, { PropTypes } from 'react';

function handleEvent(handler, day, modifiers) {
  if (!handler) {
    return undefined;
  }
  const dayState = {};
  modifiers.forEach(modifier => { dayState[modifier] = true; });
  return e => {
    e.persist();
    handler(e, day, dayState);
  };
}
export default function Day({
  day,
  tabIndex,
  empty,
  modifiers,
  onMouseEnter,
  onMouseLeave,
  onClick,
  onKeyDown,
  onTouchStart,
  onTouchEnd,
  onFocus,
  ariaLabel,
  ariaDisabled,
  ariaSelected,
  children,
  styling,
  isHovered,
  isOutside,
}) {
  if (empty) {
    return (
      <div
        role="gridcell"
        aria-disabled
        {...styling('dayPickerDay', day, modifiers, isHovered)}
      />
    );
  }
  return (
    <div
      {...styling('dayPickerDay', day, modifiers, isHovered)}
      tabIndex={tabIndex}
      role="gridcell"
      aria-label={ariaLabel}
      aria-disabled={ariaDisabled.toString()}
      aria-selected={ariaSelected.toString()}
      onClick={handleEvent(onClick, day, modifiers)}
      onKeyDown={handleEvent(onKeyDown, day, modifiers)}
      onMouseEnter={handleEvent(onMouseEnter, day, modifiers)}
      onMouseLeave={handleEvent(onMouseLeave, day, modifiers)}
      onTouchEnd={handleEvent(onTouchEnd, day, modifiers)}
      onTouchStart={handleEvent(onTouchStart, day, modifiers)}
      onFocus={handleEvent(onFocus, day, modifiers)}
      data-day-type={isOutside ? 'outside' : 'inside'}
    >
      {children}
    </div>
  );
}

Day.propTypes = {
  day: PropTypes.instanceOf(Date).isRequired,
  children: PropTypes.node.isRequired,
  styling: PropTypes.func.isRequired,

  ariaDisabled: PropTypes.bool,
  ariaLabel: PropTypes.string,
  ariaSelected: PropTypes.bool,
  empty: PropTypes.bool,
  modifiers: PropTypes.array,
  onClick: PropTypes.func,
  onKeyDown: PropTypes.func,
  onMouseEnter: PropTypes.func,
  onMouseLeave: PropTypes.func,
  onTouchEnd: PropTypes.func,
  onTouchStart: PropTypes.func,
  onFocus: PropTypes.func,
  tabIndex: PropTypes.number,
  isHovered: PropTypes.bool,
  isOutside: PropTypes.bool,
};

Day.defaultProps = {
  modifiers: [],
  empty: false,
};
